/**
 * ,,data-index,
 *   v-auto-focus="focusCtrl" :data-action="actionType" :data-current="focusIndex"
 *  data-index="index"
 */
export default {
    /**
     * ,data-index,
     * @param el
     */
    inserted: function (el) {
        const allFocusEls = getAllFocusEls(el)  // input
        let current = getTargetIndex(el,allFocusEls)
        if (!current || current < 0 || current >= allFocusEls.length) {  // data-current,current,
            current = 0
        }
        const currentEl = allFocusEls[current]

        autoFocus(currentEl)
    },
    /**
     * ,focusCtrl,actionType,
     * @param el
     * @param value
     * @param oldValue
     */
    update: function (el,{value,oldValue}) {
        if (value !== oldValue) {
            focusCtrl(el)
        }
    },
}

/**
 * input
 * @param focusEl {Node} 
 * inputtextarea,inputtextarea
 */
const autoFocus = function (focusEl) {
    let elInput = focusEl
    if (['INPUT','TEXTAREA'].indexOf(focusEl.tagName.toUpperCase()) === -1) {
        elInput = focusEl.querySelector('input,textarea')
    }
    elInput && elInput.focus()
}
/**
 * 
 * @param el {Node} 
 * @returns {NodeList} 
 */
const getAllFocusEls = function (el) {
    return el.querySelectorAll('[data-index]')
}

/**
 * 
 * next 
 * prev 
 * first 
 * last 
 * jump 
 * @param el
 */
const focusCtrl = function (el) {
    const action = el.dataset.action
    const allFocusEls = getAllFocusEls(el)
    const focusLen = allFocusEls.length
    let current = getTargetIndex(el,allFocusEls)
    switch (action) {
        case 'next':  // actionnext,
            if (current >= focusLen - 1) {
                current = focusLen - 1
            } else {
                current++
            }
            autoFocus(allFocusEls[current])
            break
        case 'prev':  // actionprev,
            if (current <= 0) {
                current = 0
            } else {
                current--
            }
            autoFocus(allFocusEls[current])
            break
        case 'first': // first,
            current = 0
            autoFocus(allFocusEls[current])
            break;
        case 'last': // last,
            current = focusLen - 1
            autoFocus(allFocusEls[current])
            break
        case 'jump': // jump,focusIndex,
            if (current >= 0 && current < focusLen) {
                autoFocus(allFocusEls[current])
            }
            break
    }
}

const getTargetIndex = function(el,collection) {
    const target = document.querySelector(`[data-index="${el.dataset.current}"]`)
    return Array.from(collection).indexOf(target)
}



